export default (str, options) => {
    if (typeof str !== 'string') {
        throw new TypeError('expected a string');
    }

    return str.trim()
        .replace(/([a-z])([A-Z])/g, '$1-$2')
        .replace(/\W/g, m => /[-]/.test(m) ? m : '-')
        .replace(/^-+|-+$/g, '')
        .replace(/-{2,}/g, m => options && options.condense ? '-' : m)
        .toLowerCase();
};
